body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

ul {
  list-style: none;
}

li {
  background: #e5e6e7;
  padding:  1rem;
  border-radius: 2px;
}

.icon {
  font-size: 3rem;
}
button {
  border: none;
  float:  right;
  padding: 1rem 2rem;
  margin: 0;
  text-decoration: none;
  /* background: #4455aa; */
  color: #4455aa;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  font-size: 1rem;
  cursor: pointer;
  text-align: center;
  transition: background 250ms ease-in-out, 
              -webkit-transform 150ms ease;
  transition: background 250ms ease-in-out, 
              transform 150ms ease;
  transition: background 250ms ease-in-out, 
              transform 150ms ease, 
              -webkit-transform 150ms ease;
  -webkit-appearance: none;
  -moz-appearance: none;
}

button:hover,
button:focus {
  color: #334499;
}

button:focus {
  outline: 1px solid #334499;
  outline-offset: -4px;
}

button:active > .icon {
  -webkit-transform: scale(0.99);
          transform: scale(0.99);
}
.Coverage {
    background: #f5f6f7;
    border-radius: 2px;
    padding: 1rem 3rem;
    margin-bottom: 3rem;
}
.Coverage h2 {
    margin-bottom: 0;
}
.Coverage h3 {
    margin-bottom: 0;
}
.Coverage p {
    margin-top: 0;
}
.Coverage .dim {
    color: #aaa;
}
.Coverage form {
    margin-top: 1rem;
}
.Plan {
    border-right: dashed 1rem #4455aa;
    min-height: 100vh;
    padding: 0 1rem;
}
.Plan ul {
    display: grid;
    grid-template-columns: 1fr;
    padding-left: 0;
}
.App > div{
  display: grid;
  grid-template-columns: 1.75fr 1fr;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

